.alpha-obj-edit{
    padding: 20px;
    border-radius: 2px;
    border: 1px solid #d6d6d6;
    background-color: #f1f1f1f1;

    .mark-as-correct{
    color: #ffd700;
    cursor: pointer;
    position: relative;
    top: -3px;
    }
    .answer-delete{
        color: #ff0000;
        position: relative;
        top: -5px;

        &:hover{
            color: #c20000!important;
        }
    }
}